html {
  scroll-behavior: smooth;
}

body {
  color: #444;
  font-family: Open Sans, sans-serif;
}

a {
  color: #5fcf80;
  text-decoration: none;
}

a:hover {
  color: #86db9f;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Raleway, sans-serif;
}

.back-to-top {
  visibility: hidden;
  opacity: 0;
  z-index: 996;
  width: 40px;
  height: 40px;
  background: #5fcf80;
  border-radius: 50px;
  transition: all .4s;
  position: fixed;
  bottom: 15px;
  right: 15px;
}

.back-to-top i {
  color: #fff;
  font-size: 28px;
  line-height: 0;
}

.back-to-top:hover {
  color: #fff;
  background: #7ed899;
}

.back-to-top.active {
  visibility: visible;
  opacity: 1;
}

#preloader {
  z-index: 9999;
  background: #fff;
  position: fixed;
  inset: 0;
  overflow: hidden;
}

#preloader:before {
  content: "";
  width: 60px;
  height: 60px;
  border: 6px solid #fff;
  border-color: #fff #5fcf80;
  border-radius: 50%;
  animation: animate-preloader 1s linear infinite;
  position: fixed;
  top: calc(50% - 30px);
  left: calc(50% - 30px);
}

@keyframes animate-preloader {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 768px) {
  [data-aos-delay] {
    transition-delay: 0 !important;
  }
}

#header {
  z-index: 997;
